import React, { Component } from "react";
import { Container, Row, Button } from "reactstrap";
import { Redirect } from "react-router-dom";
import Member from "./Member";
import Header from "components/Headers/Header.js";
import axios from "axios";
import jwt_decode from "jwt-decode";
import Swal from "sweetalert2";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";

function hapus(id) {
  const data = {
    memberId: id,
  };
  axios
    .post("https://api.clan486.or.id/member/delete", data, {
      headers: {
        Authorization: `Bearer ${localStorage.getItem("token")}`,
        Accept: `application/json`,
      },
    })
    .then((res) => {
      console.log(res);
      Swal.fire({
        title: "Delete Success",
        icon: "success",
        confirmButtonText: "Done",
      });
    })
    .catch((err) => console.log(err));
}

function bukanAdmin() {
  Swal.fire({
    title: "Access Denied!",
    icon: "error",
    confirmButtonText: "Done",
  });
}

function yakin(id) {
  Swal.fire({
    title: "Are you sure?",
    text: "You won't be able to revert this!",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      hapus(id);
      Swal.fire("Deleted!", "Your file has been deleted.", "success");
    }
  });
}

function element(id) {
  const decode = jwt_decode(localStorage.getItem("token"));
  return (
    <div>
      <Button
        color="aqua"
        className="mr-2"
        onClick={decode.admin == false ? bukanAdmin : () => yakin(id)}
      >
        <FontAwesomeIcon icon={faTrash} />
        Delete
      </Button>
    </div>
  );
}
export class Tbl extends Component {
  state = {
    users: [],
  };

  render() {
    const decode = jwt_decode(localStorage.getItem("token"));
    axios.get("https://api.clan486.or.id/member").then((res) => {
      this.setState({
        users: res.data.member.map((data) => ({
          nama: data.nama,
          nim: data.nim,
          prodi: data.prodi.prodi,
          email: data.email,
          telp: data.telp,
          action: element(data._id),
        })),
      });
    });
    return (
      <>
        <Header />
        {/* Page content */}
        <Container className="mt-7" fluid>
          <Row>
            <div>
              <Member users={this.state.users} />
            </div>
          </Row>
        </Container>
      </>
    );
  }
}
