import React, { useState, Component } from "react";
import {
  Container,
  Row,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
} from "reactstrap";
import { Redirect } from "react-router-dom";
import Header from "components/Headers/Header.js";
import QrReader from "react-qr-reader";
import Swal from "sweetalert2";
import axios from "axios";
import jwt_decode from "jwt-decode";

const Scanner = (props) => {
  const [result, setResult] = useState("");
  const [modal, setModal] = useState(false);
  const toggle = () => setModal(!modal);
  const { className } = props;

  const nim = "18.31.0010";

  const handleScan = (data) => {
    if (data) {
      let pres = data.split(",");
      const presensi = {
        minggu: pres[0],
        absen: "Hadir",
        nim: nim,
        bulan: pres[2],
        tahun: pres[1],
      };

      axios
        .post("https://api.clan486.or.id/presensi/members", presensi, {
          headers: {
            Accept: `application/json`,
          },
        })
        .then((res) => {
          // alrt
          console.log(res);
          Swal.fire({
            title: "Presensi Success",
            icon: "success",
            confirmButtonText: "Done",
          });
        })
        .catch((err) => console.log(err));
    }
  };
  const handleError = (err) => {
    console.error(err);
  };

  return (
    <>
      <Header />
      <Container className="mt--7" fluid>
        <Row>
          <Button
            color="secondary"
            onClick={toggle}
            align="center"
            className="mt-5"
          >
            Scanner
            <Modal isOpen={modal} toggle={toggle} className={className}>
              <ModalHeader toggle={toggle}>Scanner</ModalHeader>
              <ModalBody>
                <div>
                  <h3>Scanner Qr Code</h3>
                  <QrReader
                    delay={500}
                    onError={handleError}
                    onScan={handleScan}
                    style={{ width: "100%" }}
                  />
                  <h3>Scanned By Webcam Code : {result}</h3>
                </div>
              </ModalBody>
              <ModalFooter>
                <Button color="primary" onClick={toggle}>
                  Done
                </Button>
              </ModalFooter>
            </Modal>
          </Button>
        </Row>
      </Container>
    </>
  );
};

export default Scanner;
